import React, {  useEffect, useState } from "react";
import axios from "axios";

function Mbooks(){

    const API_URL = "https://lms-backend-gts2.onrender.com/";
    const [recentAddedBooks, setRecentAddedBooks] = useState([])

    useEffect(() => {
        const getallBooks = async () => {
            const response = await axios.get(API_URL + "api/books/allbooks")
            setRecentAddedBooks(response.data)
        }
        getallBooks()
    }, [API_URL])
    return(
        <div>
            <br/><br/>
                <p className="dashboard-option-title">Books</p>
                <div className="dashboard-title-line"></div>
                <p className='AdminLine'>* For physical copies contact Library Admin</p>
                <table className='admindashboard-table'>
                    <tr>
                        <th>S.No</th>
                        <th>Book Name</th>
                        <th>Author</th>
                        <th>Category</th>
                        <th>Online Link</th>
                        <th>Copies</th>
                        <th>Added Date</th>
                    </tr>
                    {
                        recentAddedBooks.map((book, index) => {
                            return (
                                <tr key={index}>
                                    <td>{index + 1}</td>
                                    <td>{book.bookName}</td>
                                    <td>{book.author}</td>
                                    <td>{book.categories}</td>
                                    <td><a href={book.onlineLink} target='_blank'><button className='edit-button'>Online Book</button></a></td>
                                    <td>{book.bookCountAvailable}</td>
                                    <td>{book.createdAt.substring(0, 10)}</td>
                                </tr>
                            )
                        })
                    }
                </table>
            </div> 
    )
}

export default Mbooks;